	import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
	import {GetTestRecordList} from 'api/exam'
export default {
	components:{
		UniLoadMore
	},
  data() {
    return {
      value: null,
      list: [],
      pageCount: 0,
      page: 0,
	  moreStatus:"",
	  categoryid:0,
    }
  },
  /*下拉刷新*/
  onPullDownRefresh() {
  	this.getList()	 
  },
  /*滚动加载*/
  onReachBottom(){
  	this.page++
  	if(this.page<=this.pageCount){
  		this.getList(this.page)	
  	}	
  },
  methods: {
	getList(p=1){
		this.page = p
		if(this.page==1){
			this.list = []
		}
		this.moreStatus = 'loading'
		GetTestRecordList({
			categoryid: this.categoryid,
			page: p,
			pagesize: 15
		}).then(res=>{
			this.pageCount = res.pagecount
			if(this.page >= this.pageCount){
				this.moreStatus = 'noMore'
			}else{
				this.moreStatus = 'more'
			}
			if(res.result){
				this.list = this.list.concat(res.data)
			}
		})		
	},
    open(item) {
	  uni.navigateTo({
	  	url:`/pages/exam/analysis?name=analysis&id=${item.id}&type=2&paperid=0&categoryid=${item.categoryid}&title=${item.title}`
	  })
    }
  },
  onLoad(query) {
  	this.categoryid = query.categoryid
  },
  onShow() {

    this.getList();
  }
};
